.btn-group {
  margin: 10px;
}

.user-field {
  line-height: 58px;
}

.add {
  position: absolute;
  top: 10px;
  right: 10px;
}

.pagination {
  justify-content: center;
  margin-top: 80px;
}

.pagination li {
  position: relative;
  width: 35px;
  height: 35px;
  margin: 5px;
}

.pagination li a {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #212529;
  text-align: center;
  line-height: 35px;
  border-radius: 5px;
  color: #FFF;
  text-decoration: none;
}

.pagination li.active a {
  background: #dc3545;
}